:root {
  --green-color-3: #590bf5;
  --green-color-2: #3a6bff;
  --green-color-1: #222961;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 64px;
  background-color: transparent; /*투명도*/
  position: fixed;
  left: 0;
  right: 0;
  z-index: 10;
  transition: 0.4s ease;
}

.container a {
  text-decoration: none;
}
.container ul {
  padding: 0;
  list-style: none;
}

.title {
  display: flex;
  align-items: center;
}
.title__i {
  font-size: 32px;
  color: var(--green-color-1);
  margin: 0 8px 0 0;
}
.title strong {
  font-size: 32px;
  color: var(--green-color-1);
  letter-spacing: 0.5px; /*자간 설정*/
}
.option__list {
  display: flex;
}
.option__list li a {
  font-size: 16px;
  padding: 4px 12px;
  color: var(--green-color-1);
}
.icon__list {
  display: flex;
}
.icon__list li {
  font-size: 24px;
  padding: 4px 8px;
}
.icon__list li a {
  color: var(--green-color-1);
}
.null {
  width: 100%;
  height: 100px;
}

@media screen and (max-width: 1090px) {
  .option__list {
    display: none;
  }
}
